import React, { useState } from 'react';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { Button, Container, Stack } from '@mui/material';

const GoWasmComponent: React.FC = () => {
    const [unixTime, setUnixTime] = useState(0)
    const [timestampResult, setTimestampResult] = useState({
        "year": 1970,
        "month": 1,
        "day": 1,
        "hour": 8,
        "minute": 0,
        "second": 0
    })

    function goTimestampConvert() {
        let res = (window.functions.convertTimestamp!)(unixTime)
        console.log(res)
        setTimestampResult(res)
    }
    function getCurrentDateTime() {
        const now = new Date();
        const year = now.getFullYear().toString();
        const month = (now.getMonth() + 1).toString().padStart(2, '0'); // Months are zero-based
        const day = now.getDate().toString().padStart(2, '0');
        const hours = now.getHours().toString().padStart(2, '0');
        const minutes = now.getMinutes().toString().padStart(2, '0');
        const seconds = now.getSeconds().toString().padStart(2, '0');

        return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
    }
    console.log(getCurrentDateTime())

    return (
        <div>
            <Container fixed>
                <Stack spacing={3}>
                    <Stack spacing={2}>
                        <TextField label="Unix Timestamp" variant="outlined" onChange={(e: React.ChangeEvent<HTMLInputElement>) => { setUnixTime(parseInt(e.target.value)) }} />
                        <Button variant='contained' onClick={goTimestampConvert}>Decode TimeStamp</Button>
                        <Typography variant="h5" component="h5">Year:{timestampResult.year} Month:{timestampResult.month} Day:{timestampResult.day} Hour: {timestampResult.hour} Minute: {timestampResult.minute} Second: {timestampResult.second}</Typography>
                    </Stack>
                </Stack>
            </Container>
        </div>
    );
}

export default GoWasmComponent;